import axios from "axios";
const crypto = require('crypto')
import store from '../store'

const headers = {
    'Content-Type': 'application/json'
}

const url = process.env.NODE_ENV === "production" ? "http://aantos.com" : "http://localhost:80"

function getAuthHeader() {
    return {Authorization: "JWT " + store.getters.get_jwt}
}

function hash(str) {
    const hash = crypto.createHash('sha256')
    str = hash.update(str, 'utf-8')
    str = str.digest('hex')
    return str
}

const api = {
    get_farms: async () => {
        const auth = getAuthHeader()
        return await axios.get(url + '/farm/', {headers: auth})
    },
    get_farm: async (farm_id) => {
        const auth = getAuthHeader()
        return await axios.get(url + '/farm/' + farm_id, {headers: auth})
    },
    get_farm_worker: async (farm_id, rig_id) => {
        const auth = getAuthHeader()
        return await axios.get(url + '/farm/' + farm_id + '/' + rig_id, {headers: auth})
    },
    post_farm: async({name}) => {
        return await axios.post(url + '/farm/', {
            name: name,
            workers: []
        }, {headers: headers})
    },
    post_worker: async (farm_id) => {
        return await axios.post(url + '/worker', {
            farm_id: farm_id
        }, {headers: headers})
    },
    post_login: async ({login, password}) => {
        const auth = getAuthHeader()
        return await axios.post(url + '/user/login', {
            username: login,
            password: hash(password)
        }, {headers: {...headers, ...auth}})
    },
    post_register: async ({username, email, password}) => {
        return await axios.post(url + '/user/register', {
            username: username,
            email: email,
            password: hash(password)
        }, {headers: headers})
    },
    get_wallets: async() => {
        const auth = getAuthHeader()
        return await axios.get(url + '/wallet', {headers: auth})
    },
    get_flight_sheets: async() => {
        const auth = getAuthHeader()
        return await axios.get(url + '/flightsheet', {headers: auth})
    },
    check_auth: async () => {
        const header = getAuthHeader()
        return await axios.get(url + '/security', {headers: header})
    },
    get_farm_wallets: async(farm_id) => {
        const auth = getAuthHeader()
        return await axios.get(url + '/wallet/' + farm_id, {headers: auth})
    },
    post_farm_wallet: async(farm_id, wallet) => {
        const auth = getAuthHeader()
        return await axios.post(url + '/wallet/' + farm_id, {wallet: wallet}, {
            headers: {...headers, ...auth}
        })
    },
    delete_farm_wallet: async(address) => {
        const auth = getAuthHeader()
        return await axios.delete(url + '/wallet?wallet_address=' + address, {
            headers: auth
        })
    },
    put_profile: async(profile) => {
        const auth = getAuthHeader()
        return await axios.put(url + '/user/profile', profile, {
            headers: {...auth, ...headers}
        })
    },
    get_farm_flightsheet: async(farm_id) => {
        const auth = getAuthHeader()
        return await axios.get(url + '/flightsheet/' + farm_id, {headers: auth})
    },
    post_command: async (command, rig_id) => {
        const auth = getAuthHeader()
        return await axios.post(url + '/command/' + rig_id, {
            command: command
        }, {headers: {...auth, ...headers}})
    }
}

export default api