<template>
  <div class="round-status" :class="colorStatus">
  </div>
</template>

<script>
export default {
  name: "RoundStatus",
  props: {
    type: String
  },
  computed: {
    colorStatus() {
      switch (this.type) {
        case 'success':
          return 'status-success'
        case 'error':
          return 'status-error'
        default:
          return ''
      }
    }
  }
}
</script>

<style scoped>
.round-status {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
}

.status-success {
  background-color: green;
}

.status-error {
  background-color: red;
}
</style>