import { createRouter, createWebHashHistory } from 'vue-router'
import Dashboard from '../views/Dashboard.vue'
import Farm from "../views/Farm"
import Rig from '../views/Rig'
import Home from '../views/Home'
import api from '../api/antos-api'
import store from '../store'
import Account from "@/views/Account";

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/farm/:id',
    name: 'Farm',
    component: Farm,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/farm/:id/:rig',
    name: 'Rig',
    component: Rig,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: Dashboard,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/account',
    name: 'Account',
    component: Account,
    meta: {
      requiresAuth: true
    }
  },
  {
    path: '/:pathMatch(.*)*',
    redirect: {
      name: 'Home'
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  if(to.matched.some(record => record.meta.requiresAuth)) {
    api.check_auth().then(resp => {
      if(resp.status === 200) {
        store.dispatch('save_user', resp.data)
        next()
      }
    }).catch(() => {
      store.dispatch('delete_jwt')
      store.dispatch('delete_user')
      next('/?jwt_invalid=1')
    })
  }
  else {
    next()
  }
})

export default router
