<template>
  <div class="account">
    <el-container class="container" direction="vertical">
      <el-row>
        <el-col>
          <h2>Account profile settings</h2>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12" :xs="24">
          <h3>Profile</h3>
        </el-col>
        <el-col :span="12" :xs="24">
          <el-form label-position="top" :model="form">
            <el-form-item label="Username" required>
              <el-input type="text" placeholder="Enter your login" v-model="form.username"/>
            </el-form-item>
            <el-form-item label="Full name">
              <el-input type="text" placeholder="Enter your full name" v-model="form.fullname"/>
            </el-form-item>
            <el-form-item label="Email" required>
              <el-input type="email" placeholder="Enter your email" v-model="form.email"/>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col>
          <el-button type="primary" style="float: right;" :disabled="saveProfileDisabled"
            @click="saveNewProfile">Save profile</el-button>
        </el-col>
      </el-row>
      <el-divider/>
      <el-row>
        <el-col :span="12" :xs="24">
          <h3>Password</h3>
          <small>Option for changing your current password</small>
        </el-col>
        <el-col :span="12" :xs="24">
          <el-form :model="formPassword" label-position="top">
            <el-form-item label="Current password">
              <el-input type="password" placeholder="Enter your current password" v-model="formPassword.currentPassword"/>
            </el-form-item>
            <el-form-item label="New password">
              <el-input type="password" placeholder="Enter your new password" v-model="formPassword.newPassword"/>
            </el-form-item>
            <el-form-item label="Confirm new password">
              <el-input type="password" placeholder="Confirm your new password" v-model="formPassword.newPasswordConfirm"/>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col>
          <el-button style="float: right;" type="primary" :disabled="savePasswordDisabled">Save new password</el-button>
        </el-col>
      </el-row>
      <el-divider/>
      <el-row>
        <el-col :span="12" :xs="24">
          <h3>Authentication history</h3>
          <small>Option for showing your last connections</small>
        </el-col>
        <el-col :span="12" :xs="24">
          <el-space v-for="(entry, idx) in loginHistory" :key="entry" size="20">
            <span style="color: var(--el-color-primary);">{{entry.os}}</span>
            <span style="color: var(--el-color-warning);">{{entry.ip}}</span>
            <span style="color: var(--el-color-error);">{{entry.browser}}</span>
            <span style="color: var(--el-color-error);">{{entry.version}}</span>
            <span style="color: var(--el-color-info);">{{formatTimestamp(entry.timestamp.$date)}}</span>
            <span v-if="idx === loginHistory.length-1" style="color: var(--el-color-success);">
              <span class="mdi mdi-check"></span> Current
            </span>
          </el-space>
          <el-divider/>
        </el-col>
      </el-row>
    </el-container>
  </div>
</template>

<script>
import api from "@/api/antos-api";
export default {
  name: "Account",
  data() {
    return {
      form: {
        username: '',
        fullname: '',
        email: ''
      },
      formPassword: {
        currentPassword: '',
        newPassword: '',
        newPasswordConfirm: ''
      },
      loginHistory: []
    }
  },
  mounted() {
    this.form.username = this.$store.getters.get_user.username
    this.form.fullname = this.$store.getters.get_user.fullname
    this.form.email = this.$store.getters.get_user.email
    this.loginHistory = this.$store.getters.get_user.auth_history
    console.log(this.$store.getters.get_user)
  },
  methods: {
    formatTimestamp(datetime) {
      const d = new Date(0)
      d.setUTCMilliseconds(datetime)
      return d.toUTCString()
    },
    saveNewProfile() {
      api.put_profile(this.form).then(resp => {
        if(resp.status === 200) {
          this.$router.go()
        }
      })
    }
  },
  computed: {
    savePasswordDisabled() {
      return this.formPassword.currentPassword === '' || this.formPassword.newPassword === ''
        || this.formPassword.newPasswordConfirm === ''
    },
    saveProfileDisabled() {
      return this.form.email === '' || this.form.username === ''
    }
  }
}
</script>

<style scoped>
.container {
  padding: 24px;
  margin-left: 15%;
  margin-right: 15%;
}

h3 {
  font-weight: bold;
}

small {
  color: slategray;
}
</style>