import { createStore } from 'vuex'

const store = createStore({
  state: {
    jwt: localStorage.getItem('jwt') || '',
    user: JSON.parse(localStorage.getItem('user')) || {}
  },
  mutations: {
    jwt(state, newjwt) {
      localStorage.setItem('jwt', newjwt)
      state.jwt = newjwt
    },
    user(state, newuser) {
      localStorage.setItem('user', JSON.stringify(newuser))
      state.user = newuser
    }
  },
  actions: {
    save_jwt(context, newjwt) {
      context.commit('jwt', newjwt)
    },
    delete_jwt(context) {
      context.commit('jwt', '')
    },
    save_user(context, newuser) {
      context.commit('user', newuser)
    },
    delete_user(context) {
      context.commit('user', {})
    }
  },
  getters: {
    get_jwt: state => {
      return state.jwt
    },
    get_user: state => {
      return state.user
    }
  }
})

export default store