<template>
  <div class="flightsheet">
    <el-row class="newBtnRow" justify="end">
      <el-col :span="3" :xs="24">
        <el-button type="primary" @click="newDialogVisible = true"><span class="mdi mdi-plus"></span> Nouveau</el-button>
      </el-col>
    </el-row>
    <el-empty description="No flight sheet found" v-if="flightsheets.length === 0"/>
    <div v-else ref="fs_collapse">
      <el-collapse class="flight-collapses" v-for="(sheet, idx) in flightsheets" :key="sheet">
        <el-collapse-item :name="idx" class="flight-collapse">
          <template #title>
            <el-space direction="horizontal">
              <h4><span class="mdi mdi-ethereum"></span> {{sheet.coin}}</h4>
              <p>{{sheet.name}}</p>
              <p>{{sheet.pool.name}}</p>
              <p>{{sheet.miner}}</p>
            </el-space>
          </template>
          <el-descriptions title="Flight Sheet infos" direction="vertical">
            <el-descriptions-item label="Wallet Address:">{{sheet.wallet[0].address}}</el-descriptions-item>
            <el-descriptions-item label="Server URLs:">
              <div v-for="url in sheet.pool.urls" :key="url">{{url}}</div>
            </el-descriptions-item>
          </el-descriptions>
        </el-collapse-item>
      </el-collapse>
    </div>
    <el-dialog title="New flightsheet" v-model="newDialogVisible">
      <template #footer>
        <el-button @click="newDialogVisible = false">Cancel</el-button>
        <el-button type="primary">Save</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Flightsheet",
  props: {
    flightsheets: Array
  },
  data() {
    return {
      newDialogVisible: false
    }
  }
}
</script>

<style scoped>
.flight-collapse {
  margin: 10px;
}

.flight-collapses {
  background-color: white;
}

.newBtnRow {
  margin-bottom: 10px;
}
</style>