<template>
  <div class="home">

  </div>
</template>

<script>
import {ElNotification} from "element-plus";

export default {
  name: "Home",
  mounted() {
    if(this.$route.query.jwt_invalid && this.$route.query.jwt_invalid === '1') {
      ElNotification({
        title: "Session expired",
        message: "Your session has expired. Please login again",
        type: "warning",
        offset: 40
      })
    }
  }
}
</script>

