<template>
  <div class="dashboard">
    <el-row style="margin-top: 20px; margin-bottom: 20px;">
      <el-col :span="21">
        <el-breadcrumb>
          <el-breadcrumb-item>Home</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <div class="dashboard-header">
      <el-row v-if="activeTab === 'farm'" class="is-justify-space-around">
        <el-col :span="3" :xs="11">
          <el-card class="header-cards">
            <template #header>
              <div class="header-cards-title">
                <span>Total power consumption</span>
              </div>
            </template>
            <div class="header-cards-body">
              {{totalPower}}W <span class="mdi mdi-flash" style="color: yellowgreen;"></span>
            </div>
          </el-card>
        </el-col>
        <el-col :span="3" :xs="11">
          <el-card class="header-cards">
            <template #header>
              <div class="header-cards-title">
                <span>Total online workers</span>
              </div>
            </template>
            <div class="header-cards-body">
              {{totalOnline}} Online <span class="mdi mdi-check" style="color: green;"></span>
            </div>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <el-tabs v-model="activeTab">
      <el-tab-pane label="Farms" name="farm">
        <el-row justify="end">
          <el-col :span="3" :xs="24">
            <el-button type="primary" @click="newDialogVisible = true"><span class="mdi mdi-plus"></span> Nouveau</el-button>
          </el-col>
        </el-row>
        <el-row :gutter="16" v-loading="loading" style="margin-top: 10px;">
          <el-col v-for="farm in farms" :key="farm" :span="8" :xs="24" class="el-farm-col">
            <FarmCard :title="farm.name" v-on:click="() => this.farm_click(farm['_id']['$oid'])" :workers="farm.workers"/>
          </el-col>
        </el-row>
        <el-dialog v-model="newDialogVisible" title="Nouveau farm">
          <el-form :model="newForm">
            <el-form-item label="Nom">
              <el-input v-model="newForm.name" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <template #footer>
            <el-button @click="newDialogVisible = false">Annuler</el-button>
            <el-button type="primary" :disabled="canSaveNew" @click="saveNewFarm" :loading="newForm.saveLoading">Sauvegarder</el-button>
          </template>
        </el-dialog>
      </el-tab-pane>
      <el-tab-pane label="Wallets" name="wallet">
        <Wallets :wallets="wallets" @saveWallet="saveNewWallet"/>
      </el-tab-pane>
      <el-tab-pane label="Flight Sheets" name="flight sheet">
        <Flightsheet :flightsheets="flightsheets"/>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import api from "@/api/antos-api";
import FarmCard from "@/components/FarmCard";
import Wallets from "@/components/Wallets";
import Flightsheet from "@/components/Flightsheet";

export default {
  name: 'Dashboard',
  components: {Flightsheet, Wallets, FarmCard},
  data() {
    return {
      farms: [],
      loading: true,
      newDialogVisible: false,
      newForm: {
        name: '',
        saveLoading: false
      },
      activeTab: 'farm',
      wallets: [],
      flightsheets: []
    }
  },
  mounted() {
    api.get_farms().then(resp => {
      this.farms = resp.data
      this.loading = false
    })
    api.get_wallets().then(resp => {
      if(resp.status === 200) {
        this.wallets = resp.data
      }
    })
    api.get_flight_sheets().then(resp => {
      if(resp.status === 200) {
        this.flightsheets = resp.data
      }
    })
  },
  methods: {
    farm_click: function(farm_id) {
      this.$router.push('/farm/' + farm_id)
    },
    saveNewFarm: function() {
      this.newForm.saveLoading = true
      api.post_farm(this.newForm).then(resp => {
        if(resp.status === 201) {
          this.$router.go()
        }
      })
    },
    saveNewWallet(wallet) {
      api.post_farm_wallet("null", wallet).then(() => {
        this.$router.go()
      })
    },
    accumulator(curr, next) {
      return curr + next
    }
  },
  computed: {
    canSaveNew: function() {
      return this.newForm.name === ''
    },
    totalPower: function() {
      return this.farms.map(value => value.workers.map(value => value.stats?.power.reduce(this.accumulator, 0) | 0).reduce(this.accumulator)).reduce(this.accumulator, 0)
    },
    totalOnline: function () {
      return this.farms.map(value => value.workers.filter(value => value.stats?.power.reduce(this.accumulator, 0) > 0).length).length
    }
  }
}
</script>

<style scoped>
.el-farm-col {
  margin-bottom: 16px;
}

.header-cards {
  background-color: #FEFBF3;
}

.dashboard-header {
  background-color: #F8F0DF;
  padding-top: 10px;
  padding-bottom: 5px;
}

.dashboard {
  margin-top: 0;
}

.header-cards-title {
  font-size: 1.2rem;
  font-weight: bold;
}

.header-cards-body {
  font-size: 1.2rem;
}
</style>