<template>
  <div class="farm-card">
    <el-card :class="'box-card-' + (is_online ? 'online' : 'offline')" shadow="hover">
      <template #header>
        <div class="card-header">
          <span class="card-title">{{title}}</span>
          <el-tag type="info" class="card-header-tag">{{workers.length}} worker{{plurial_worker}}</el-tag>
        </div>
      </template>
      <el-row>
        <el-col :span="12">
          <span>{{total_power}}W <span class="mdi mdi-flash"></span></span>
        </el-col>
        <el-col :span="12">
          <span>{{total_khs > 1000 ? total_khs/1000 + 'mh/s' : total_khs + 'kh/s'}} <span class="mdi mdi-pickaxe"></span></span>
        </el-col>
      </el-row>
      <el-row class="second-row">
        <el-col :span="12">
          <span style="color: var(--el-color-success);">{{online_workers}} Online</span>
        </el-col>
        <el-col :span="12">
          <span>{{total_gpus}} GPUs</span>
        </el-col>
      </el-row>
    </el-card>
  </div>
</template>

<script>
export default {
  name: "FarmCard",
  props: {
    title: String,
    workers: Array
  },
  methods: {
    reducing_acc: (prev, curr) => {
      return prev + curr
    }
  },
  computed: {
    plurial_worker: function () {
      return this.workers.length <= 1 ? '' : 's'
    },
    total_khs: function () {
      if(this.workers.length > 0) {
        return this.workers.map(value => value.stats?.total_khs | 0).reduce(this.reducing_acc)
      }
      return 0
    },
    total_power: function () {
      return this.workers.length > 0 ? this.workers.map((value) => value.stats?.power.reduce(this.reducing_acc) | 0).reduce(this.reducing_acc) : 0
    },
    is_online: function () {
      return this.total_khs > 0
    },
    online_workers: function() {
      return this.workers.filter(value => value.stats?.power.reduce(this.reducing_acc) > 0).length
    },
    total_gpus: function() {
      return this.workers.map(value => value.hardwares?.gpu).length
    }
  }
}
</script>

<style scoped>
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.card-header > .card-title {
  font-weight: bold;
  font-size: 1.7rem;
}

.farm-card {
  cursor: pointer;
}

.mdi-flash {
  color: yellowgreen;
}

.mdi-pickaxe {
  color: blue;
}

.box-card-online {
  border-top-color: green;
}

.box-card-offline {
  border-top-color: red;
}

.second-row {
  margin-top: 10px;
}

span {
  font-size: 1.2rem;
}
</style>