<template>
  <div class="wallets">
    <el-row class="newBtnRow" justify="end">
      <el-col :span="3" :xs="24">
        <el-button type="primary" @click="newDialogVisible = true"><span class="mdi mdi-plus"></span> Nouveau</el-button>
      </el-col>
    </el-row>
    <el-empty description="No wallet found" v-if="wallets.length === 0"/>
    <el-table v-else :data="wallets">
      <el-table-column type="selection"/>
      <el-table-column label="Coin">
        <template #default="scope">
          <span class="mdi mdi-ethereum"></span> {{scope.row.coin}}
        </template>
      </el-table-column>
      <el-table-column prop="name" label="Name"/>
      <el-table-column prop="address" label="Address"/>
      <el-table-column prop="balance" label="Balance"/>
      <el-table-column label="Action">
        <template #default="scope">
          <el-button type="danger" size="mini" @click="() => deleteWallet(scope.row)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog v-model="newDialogVisible" title="New wallet">
      <el-form :model="newForm">
        <el-form-item label="Coin" required>
          <el-select v-model="newForm.coin" placeholder="Select a coin">
            <el-option label="ETH" value="eth"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Address" required>
          <el-input placeholder="Enter a wallet address" v-model="newForm.address"/>
        </el-form-item>
        <el-form-item label="Name">
          <el-input placeholder="Enter a wallet name" v-model="newForm.name"/>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="newDialogVisible = false">Cancel</el-button>
        <el-button type="primary" :disabled="saveDisabled" @click="saveWallet">Save</el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "Wallets",
  props: {
    wallets: Array
  },
  data() {
    return {
      newDialogVisible: false,
      newForm: {
        coin: '',
        address: '',
        name: ''
      }
    }
  },
  computed: {
    saveDisabled() {
      return this.newForm.coin === '' || this.newForm.name === '' || this.newForm.address === ''
    }
  },
  methods: {
    saveWallet() {
      this.newDialogVisible = false
      this.$emit('saveWallet', this.newForm)
    },
    deleteWallet(wallet) {
      this.$emit('deleteWallet', wallet.address)
    }
  }
}
</script>

<style scoped>
.newBtnRow {
  margin-bottom: 1em;
}
</style>