<template>
  <div class="app">
    <TopAppBar>
      <div v-if="$store.getters.get_jwt === ''">
        <TopAppBarItem name="signup" iconclass="mdi mdi-pencil" @click="registerDialogVisible = true">Signup</TopAppBarItem>
        <TopAppBarItem type="primary" name="login" iconclass="mdi mdi-login" @click="loginDialogVisible = true">Login</TopAppBarItem>
      </div>
      <div v-else>
        <el-dropdown>
          <TopAppBarItem name="profile" type="primary" iconclass="mdi mdi-account">{{$store.getters.get_user.username}} <span class="mdi mdi-menu-down"></span></TopAppBarItem>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="() => this.$router.push('/dashboard')">
                <span class="mdi mdi-home"></span> Dashboard
              </el-dropdown-item>
              <el-divider style="margin: 0;"/>
              <el-dropdown-item @click="() => this.$router.push('/account')">
                <span class="mdi mdi-account"></span> Account
              </el-dropdown-item>
              <el-dropdown-item @click="signout">
                <span class="mdi mdi-exit-run"></span> Sign out
              </el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </TopAppBar>
    <el-dialog v-model="loginDialogVisible" title="Login">
      <el-form :model="loginForm" label-position="top">
        <el-form-item label="Username:" :error="loginForm.userFound ? '' : 'User not found. Please check the username and password'">
          <el-input v-model="loginForm.username" placeholder="Enter your username or email here"></el-input>
        </el-form-item>
        <el-form-item label="Password:" :error="loginForm.userFound ? '' : 'User not found. Please check the username and password'">
          <el-input type="password" v-model="loginForm.password" placeholder="Enter your password here"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="loginDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="login" :loading="loginForm.loginLoading">Login</el-button>
      </template>
    </el-dialog>
    <el-dialog v-model="registerDialogVisible" title="Register">
      <el-form :model="registerForm" label-position="top">
        <el-form-item label="Username:">
          <el-input v-model="registerForm.username" placeholder="Enter your username here"></el-input>
        </el-form-item>
        <el-form-item label="Email:">
          <el-input type="email" v-model="registerForm.email" placeholder="Enter your email here"></el-input>
        </el-form-item>
        <el-form-item label="Password:" :error="registerPasswordsMatch">
          <el-input type="password" v-model="registerForm.password" placeholder="Enter your password here"></el-input>
        </el-form-item>
        <el-form-item label="Password confirmation:" :error="registerPasswordsMatch">
          <el-input type="password" v-model="registerForm.passwordConfirm" placeholder="Confirm your password"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <el-button @click="registerDialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="register" :loading="registerForm.registerLoading">Register</el-button>
      </template>
    </el-dialog>
    <router-view class="router-view"/>
  </div>
</template>

<script>
import TopAppBar from "@/components/topbar/TopAppBar";
import TopAppBarItem from "@/components/topbar/TopAppBarItem";
import api from "@/api/antos-api";
import {ElMessage} from "element-plus";

export default {
  name: 'App',
  components: {
    TopAppBar,
    TopAppBarItem
  },
  data() {
    return {
      loginDialogVisible: false,
      loginForm: {
        username: '',
        password: '',
        userFound: true,
        loginLoading: false
      },
      registerDialogVisible: false,
      registerForm: {
        username: '',
        email: '',
        password: '',
        passwordConfirm: '',
        userExist: false,
        registerLoading: false
      }
    }
  },
  methods: {
    login() {
      this.loginForm.loginLoading = true
      api.post_login({login: this.loginForm.username, password: this.loginForm.password}).then(resp => {
        this.loginDialogVisible = false
        if(resp.status === 200) {
          const jwt = resp.data.access_token
          this.$store.dispatch('save_jwt', jwt)
          this.$router.push('/dashboard')
        }
      }).catch(() => {
        this.loginForm.userFound = false
      }).finally(() => {
        this.loginForm.loginLoading = false
      })
    },
    register() {
      this.registerForm.registerLoading = true
      api.post_register(this.registerForm).then(resp => {
        if(resp.status === 201) {
          this.registerDialogVisible = false
          ElMessage({
            type: 'success',
            message: 'User successfully created !'
          })
        }
      }).catch(() => {
        this.registerForm.userExist = true
      }).finally(() => {
        this.registerForm.registerLoading = false
      })
    },
    signout() {
      this.$store.dispatch('delete_jwt')
      this.$store.dispatch('delete_user')
      this.$router.push('/')
    }
  },
  computed: {
    registerPasswordsMatch() {
      return this.registerForm.password === this.registerForm.passwordConfirm ? '' : 'Passwords don\'t match'
    }
  }
}
</script>

<style>
#app {
  font-family: 'Helvetica Neue', Helvetica, 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', '', Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

body {
  margin: 0;
  background-color: #FEFBF3;
}

.el-tabs__header {
  background-color: #F8F0DF;
}
</style>

<style scoped>
.router-view {
  margin: 8px;
}
</style>